$mainColor1: white;
$mainColor2: #343434;
$mainColor3: #1DD1A1;
@mixin main-btn {
  background-color: $mainColor3;
  font-size: 14px;
  line-height: 17px;
  color: $mainColor1;
  padding: 8px;
  border: 1px solid $mainColor3;
  border-radius: 3px;
  outline: none;
  transition: linear 0.5s;
  &:hover {
    background-color: $mainColor1;
    color: $mainColor3;
  }
}

